const {EDITABLE_TYPES, DATA_TYPES} = require("../../../Common/VariousTypes");

exports.getDataType =  (typeId, id) => {
    const type = DATA_TYPES[typeId].find(d=>d.id == id)
    return type.jsonType || type.caption;
}

exports.getSchemaEntity = (allSchemas, schemaId, entityId) =>{
    const schema = allSchemas.find(schema=>schema.id == schemaId);
    if (schema){
        const entity = schema.entities.find(entity=>entity.id == entityId);
        return {schema,entity};
    }

    return {};
};

function getSchemaInfo(schemaEditable){
    const {name,schemaType,description}  = schemaEditable.properties['0'];
    const documentInfo = schemaEditable ? schemaEditable.schema : null;

    if (documentInfo){
        const entities = documentInfo.vertices.map(vertex=>{
            
            let fields = [];
            let description,alias;
            const entityProperties = schemaEditable.properties[vertex.id];
            if (entityProperties){
                fields = entityProperties.fields ? entityProperties.fields : []
                description = entityProperties.description;
                alias = entityProperties.alias;
            }
            
            return {
                id:vertex.id,
                name:vertex.value,
                fields,
                description,
                alias
            };
        });

        return {id:schemaEditable.id, name, description, type:schemaType,entities};
    }
}

exports.getSchemaInfo = getSchemaInfo;

exports.DocumentInformationRetriever = class  {

    document;

    constructor(document){
        this.document = document;
    }

    getAllSchemas(){
        const schemaEditables = this.document.editables.filter(editable=>editable.type == EDITABLE_TYPES.SCHEMA);

        let schemas = [];

        for(const schemaEditable of schemaEditables){

            const schemaInfo = getSchemaInfo(schemaEditable);
            if (schemaInfo){
                schemas.push (schemaInfo);
            }
        }

        return schemas;
    }

    getAllApis(){
        const apiEditables = this.document.editables.filter(editable=>editable.type == EDITABLE_TYPES.API);
        
        const transformedData = apiEditables.map(editable=>{
            const apiProperties = this.getProperties(editable.id,"0");
            return {
                id:editable.id,
                ...apiProperties
            }
        });
        return transformedData;
    }

    getDocumentLevelEditable(){
        const editable = this.document.editables.find(editable=>editable.type == EDITABLE_TYPES.DOCUMENT_LEVEL);
        if (editable){
            const editableProperties = this.getProperties(editable.id,"0");
            return {
                id:editable.id,
                ...editableProperties
            }
        }
    }

    getEditable(editableId){
        const editable = this.document.editables ? this.document.editables.find(editable=>editable.id == editableId) : null;
        return editable;
    }

    getEditablesByType(typeId){
        const editables = this.document.editables ? this.document.editables.filter(editable=>editable.type == typeId) : [];
        return editables;
    }

    getApiBlocklyData(editableId, workflowName="main"){
        const editable = this.getEditable(editableId);
        if (editable){
            if (editable.workflows){
                return editable.workflows[workflowName];
            }
        }
    }

    getProperties (editableId,blockId) {
        const editable = this.getEditable(editableId);
        if (editable){
            if (editable.properties){
                return editable.properties[blockId];
            }
        }
    }

    getEditableProperties(editableId){
        return this.getProperties(editableId,"0");
    }

    getEditableData(editableId){
        const editable = this.getEditable(editableId);
        return editable ? editable.data : null;
    }

    getDocumentData() {
        const allRequestsEditable = this.getDocumentLevelEditable();
        const props = this.getEditableProperties(allRequestsEditable.id);
        return props;
    }

    getInheritedSettings(configId, env){
        const config = this.document.settings.find(config => config.id == configId);
        let configData = null;
        if (config){
            const allEnv = config.environments["*"];
            const currentEnv = config.environments[env] || {};
            configData = {};
            Object.keys(allEnv).forEach(key=>{
                configData[key] = currentEnv[key] || allEnv[key]
            });
        }
        return configData;
    }
}