import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';

export default class LogViewerDialogMolecule extends AtomicDesign {

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.DIALOG.SHOW_LOGS, this.openDialogBox);
    }

    openDialogBox(data){
        this.vue.logs = data?.logs || [];
        this.vue.visible = true;
    }

}