import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';

export default class ProgressIndicatorMolecule extends AtomicDesign {

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.SHOW_BUSY_INDICATOR,this.showBusyIndicator);
    }

    showBusyIndicator(data){
        this.vue.data = data;
    }


}