import {AtomicDesign} from '../../../../../../Frontend/Core/AtomicDesign';
import {getSchemaInfo} from '../../../../../BackendServices/Converters/DocumentInformationRetriever';
import {DATA_TYPES} from '../../../../../../Common/VariousTypes';

export default class ApiRunMolecule extends AtomicDesign {
    
    editorInit (editor) {
        require('brace/ext/language_tools')
        require('brace/mode/javascript')
        require('brace/theme/twilight')

        editor.setOptions({
          fontSize: "14pt",
          showLineNumbers: false,
          showGutter:true,
          maxLines: Infinity,
          minLines: 10
        });

        editor.container.style.background = "rgb(34, 41, 51)";
    }

    getPostBodyFromSchema(){
      const properties = this.vue.selectedItems.editable.properties["0"];
      if (properties){
        const {requestSchema, requestSchemaEntity} = properties;

        if(!requestSchema){
          this.msgbox("No request schema is selected for the POST body",this.MSGBOX_TYPE.OK, "Error");
          return;
        }

        if(!requestSchemaEntity){
          this.msgbox("No request entity is selected for the POST body",this.MSGBOX_TYPE.OK, "Error");
          return;
        }

        var schema = this.vue.openedDocument.editables.find (editable => editable.id == requestSchema);

        if(!schema){
          this.msgbox("The selected schema is unavailable, it may have been deleted",this.MSGBOX_TYPE.OK, "Error");
          return;
        }

      }else {
        this.msgbox("No properties available for the selected entity",this.MSGBOX_TYPE.OK, "Error");
      }

      const schemaInfo = getSchemaInfo(schema);
      let rootObj = this.fillObjectFields(schemaInfo, properties.requestSchemaEntity);

      this.vue.selectedPreset.environments[this.vue.environment].postBody = JSON.stringify(rootObj,null,"\t")
    }


    fillObjectFields(schemaInfo, entityId){
      const entityInfo = schemaInfo.entities.find(entity => entity.id == entityId);
      if (!entityInfo) {
        return null;
      }
      let baseObject = {};

      const dataTypes = DATA_TYPES[schemaInfo.type];

      Object.keys(entityInfo.fields).forEach(fieldId => {
        const fieldObj = entityInfo.fields[fieldId];
        const canSkip = fieldObj.primaryKey && fieldObj.autoIncrement;

        if (fieldObj.name && !canSkip){
          
          const fieldDataType = dataTypes.find(dType => dType.id == fieldObj.dataType);
          const defaultValue = fieldObj.default || "";
          const isArray = fieldObj.isArray;

          let value;
          if (fieldDataType){

            const nativeType = fieldDataType.jsonType || fieldDataType.type;

            switch(nativeType){
              case "boolean":
                value = defaultValue || false;
                break;
              case "number":
                value = defaultValue || 0;
                break;
              default:
                value = `${defaultValue}`;
                break;
            }
            
          }
          else {
            value = this.fillObjectFields(schemaInfo, fieldObj.dataType);
          }

          baseObject[fieldObj.name] = isArray ? [value] : value;
          
        }
        
      });

      return baseObject;
    }

    sanitizeEntityName (name) {
      return name.replace(" ", "_").toLowerCase();
  }
}

