import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';
import Strings from '../../../../../DesignSystem/UiResources/strings';

export default class SelectTemplateDialogMolecule extends AtomicDesign {
    
    dialogBoxData = null;

    addEventListeners(){
        this.onGlobalEvent(EVENTS.TEMPLATE.SHOW_DIALOG,this.showDialogBox);
    }   

    async getAllTemplates(){       
        const params = {staticCache:true,localStorage:true,busyTitle: Strings.PROGRESS_INDICATOR.LOADING_TEMPLATES};
        const {result,error} = await this.backend(params).getAllTemplates(this.dialogBoxData.type);

        if (!error){
            this.vue.templateCategories = result;
            if (result.length >0){
                this.vue.selectedCategory = this.vue.templateCategories[0];
            }
            
        }else {
            this.msgbox("Error loading template",this.MSGBOX_TYPE.OK,"error loading templates");
        }
    }

    showDialogBox(data){
        this.vue.isCreateClicked = false;
        this.vue.showDialog = true;
        this.vue.dialogBoxTitle = data?.title || "Please select a template";
        this.dialogBoxData = data;
    }

    async createDocument(){
        const isValid = await this.validateForm();
        this.vue.isCreateClicked = true;
        if (isValid){
            const categoryKey = this.vue.selectedCategory.key;
            const templateId = this.vue.selectedTemplate.id;
            const selectedField = this.dialogBoxData.field;
            const formData = this.vue.formData;
            this.dialogBoxData.callback({
                categoryKey,
                templateId,
                formData,
                selectedField,
                type:this.dialogBoxData.type
            });

            this.resetState();
            this.vue.showDialog = false;
        }

    }

    closeDialog(){
        if (!this.vue.isCreateClicked){
            this.dialogBoxData.callback(null);
            this.resetState();
            this.vue.showDialog = false;
        }
    }

    resetState(){
        this.vue.selectedTemplate = null;
        this.vue.templateCategories = [];
        this.vue.formData = {};
        this.vue.formValidationRules = null;
    }

    validateForm (){
        return new Promise((resolve)=>{
            this.vue.$refs.settingsForm.validate((valid) => {
                resolve(valid);
            });
        });
    }

}