<template>
 
  <div class="select-template-dialog-molecule">
    <el-dialog :title="dialogBoxTitle" :visible.sync="showDialog" width="900px" top="60px" @open="getAllTemplates" @close="closeDialog" :close-on-click-modal="false">
      <div class="select-template-dialog-molecule__categories">
        <label>Category </label>
        <el-select placeholder="No template category selected" v-model="selectedCategory" :filterable="true">
          <el-option v-for="category in templateCategories" :key="category.key" :label="category.caption" :value="category"></el-option>
        </el-select>
      </div>
      <div class="select-template-dialog-molecule__contents">
        
        <div class="select-template-dialog-molecule__contents__template lc-custom-scroll">
          <div 
            v-for="template in templates" :key="template.id"
            @click="selectedTemplate = template"
            :class="{
            'select-template-dialog-molecule__contents__template__item': template != selectedTemplate, 
            'select-template-dialog-molecule__contents__template__itemselected': template == selectedTemplate
          }">
            <img v-if="template.icon" :src="getAssetImage(template.icon)" />
            <i v-if="template.iconClass" :class="template.iconClass"></i>
            <label>{{template.title}}</label>
          </div>
        </div>
        <div class="select-template-dialog-molecule__contents__settings lc-custom-scroll" >
          <el-form v-if="selectedTemplate" ref="settingsForm" :model="formData" :rules="formValidationRules" label-width="150px" label-position="left" onsubmit="return false">
            <el-form-item v-for="config of selectedTemplate.config" :key="config.key" :label="config.caption" :prop="config.key">
              <el-input v-model="formData[config.key]" v-if="config.type != 'select'" :type="config.type" @keyup.enter.native="createDocument"/>
              <el-select v-if="config.type=='select'" v-model="formData[config.key]" :filterable="true">
                <el-option v-for="(value, key) in config.typeData" :key="key" :label="value" :value="key"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showDialog = false">Cancel</el-button>
        <el-button type="primary" @click="createDocument" :disabled="!selectedTemplate">Create</el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
import SelectTemplateDialogMolecule from './SelectTemplateDialogMolecule.hook'
const hook = new SelectTemplateDialogMolecule();
hook.setBackendNamespace("UiContainers/EditorContainerOrganism/DialogBoxes").setElementName("SelectTemplateDialogMolecule");

export default {
  name: 'SelectTemplateDialogMolecule',
  beforeCreate() { hook.hook(this); hook.addEventListeners(); },
  beforeDestroy(){ hook.unsubscribeGlobalEvents(); }, 
  methods: hook.methods("getAllTemplates", "getAssetImage","createDocument","closeDialog"),
  props: {},
  computed:{
    templates(){
      return this.selectedCategory ? this.selectedCategory.templates : [];  
    },
    ...hook.getters("selectedItems")
  },
  watch : {
    selectedTemplate(val){
      let formData = {};
      if (val?.config){
        for (const config of val.config){
          formData[config.key] = config.default;
        }
      }
      this.formData = formData;
      this.formValidationRules = this.selectedTemplate?.rules;
    }
  },
  data() {
    return { 
      showDialog: false,
      selectedCategory: null,
      selectedTemplate:null,
      dialogBoxTitle:null,
      templateCategories: [],
      formData:{},
      formValidationRules:null,
      isCreateClicked: false
    }
  }
}

</script>

<style scoped lang="scss">

.select-template-dialog-molecule { 
  
  &__categories {
    margin-bottom:30px;
    display: flex;
    align-items: center;

    label {
      padding-right:80px;
      width:70px;
    }

    .el-select {
      width:100%;
    }

  }

  &__contents {
    
    display: flex;
    
    &__template {
      width: 400px;
      height:300px;
      padding:10px;
      overflow-y:auto;

      &__item:hover{
        opacity: 0.5;
      }

      @mixin __item(){
        display: table;
        width: 100%;
        transition: 0.2s;
        display: flex;
        align-content: center;
        align-items: center;
        img{
          width:30px;
          padding:10px;
          margin-right:15px;
        }

        i {
          font-size:30px;
          padding:10px;
        }
        label {
          text-align: left;
          width:100%;
        }

        > * {
          cursor: pointer;
          display: table-cell;
          vertical-align: middle;
        }
      }

      &__item {
        @include __item();
      }

      &__itemselected {
        @include __item();
        background-color: rgb(83, 83, 83);
      }
  }

  &__settings {
    margin-left:20px;
    padding-left:20px;
    padding-right:20px;
    padding-top:20px;

    width: 400px;
    height:300px;
    overflow-y:auto;
    
    .el-form-item__label {
      text-align: left;
    }

    input {
      width: 100%;
    }

    .el-select {
      width:100%;
    }



  }

  > * {
     background-color: rgb(20, 20, 20);
  }
  
 }
}

</style>
