<template>
  <div class="api-run-molecure" v-if="selectedPreset.environments[environment] && isEndpoint">

    <div class="api-run-molecure__parameters">
      <div class="api-run-molecure__parameters__group" v-if="allHeaders.length > 0">
        <p>HTTP Headers</p>
        <div class="api-run-molecure__parameters__group__controls" v-for="param of allHeaders" :key="param.id">
          <label>{{param.name}}</label>
          <el-input v-model="selectedPreset.environments[environment].headers[param.name]" size="small"/>
        </div>
      </div>

      <div class="api-run-molecure__parameters__group" v-if="pathParameters.length > 0">
        <p>Path Parameters</p>
        <div class="api-run-molecure__parameters__group__controls" v-for="param of pathParameters" :key="param.id">
          <label>{{param.name}}</label>
          <el-input v-model="selectedPreset.environments[environment].pathParameters[param.name]" size="small"/>
        </div>
      </div>
    
      <div class="api-run-molecure__parameters__group" v-if="queryParameters.length > 0">
        <p>Query Parameters</p>
        <div class="api-run-molecure__parameters__group__controls" v-for="param of queryParameters" :key="param.id">
          <label>{{param.name}}</label>
          <el-input v-model="selectedPreset.environments[environment].queryParameters[param.name]" size="small"/>
        </div>
      </div>

      <div class="api-run-molecure__parameters__post-body" v-if="selectedItems.editable.properties['0'].method != 'GET'">
        <p v-if="selectedPreset.environments[environment].postBody">Post Body</p>
        <AceEditor v-model="selectedPreset.environments[environment].postBody" @init="editorInit" lang="javascript" theme="twilight" height="200px" width="100%"></AceEditor>
        <el-button @click="getPostBodyFromSchema">Get default post body from schema</el-button>
      </div>
    </div>
    
    
  </div>
</template>

<script>
import {EDITABLE_TYPES} from '../../../../../../Common/VariousTypes';
import AceEditor from 'vue2-ace-editor';
import ApiRunMolecule from './ApiRunMolecule.hook';
const hook = new ApiRunMolecule();

export default {
  name: 'ApiRunMolecule',
  beforeCreate() { hook.hook(this)},
  components: {
    AceEditor
  },
  methods: hook.methods("editorInit", "getPostBodyFromSchema"),
  props: { 
    selectedPreset : null,
    allHeaders : Array
  },
  data() {
      return { 
        script:"{}",
        environment: "*"
      }
  },
  computed: {
    pathParameters(){
      return this.selectedEditableProperties?.pathParameters || [];
    },
    queryParameters(){
      return this.selectedEditableProperties?.queryParameters || [];
    },
    isEndpoint(){
      return (this.selectedItems?.editable.type == EDITABLE_TYPES.API) || false;
    },
    selectedEditableProperties(){
      const properties = this.selectedItems?.editable?.properties;
      return properties ? properties[0] : {};
    },
    ...hook.getters("selectedItems","selectedBlock","openedDocument")
  }
}

</script>

<style scoped lang="scss">

.api-run-molecure {
  height:300px;
  margin-right:40px;
  &__parameters {
    margin-left:5px;

    &__post-body{
      .el-button {
        margin-top:15px;
        margin-bottom:15px;
      }
    }

    p {
      margin-bottom:20px;
      font-size:16px; 
    }

    &__group{
      
      p {
        display: block;
        margin-bottom:20px;
      }

      &__controls {
        display: flex;
        align-items: center;

        label {
          width:180px;
        }
        .el-input {
          margin-bottom:5px;
          padding-left:5px;
        }
      }
    }

  }
}

</style>
