<template>
  <div class="configuration-dialog-molecule">
    <el-dialog :title="title" :visible.sync="visible" width="900px" top="80px" @close="dialogClosed" :close-on-click-modal="false">
      <div class="configuration-dialog-molecule__dialog">
        <div class="configuration-dialog-molecule__dialog__selector">
          <label>Configration</label>
          <el-select placeholder="" v-model="selectedPresetId" v-on:change="presetSelected" :filterable="true" size="small">
            <el-option :value="null" label=""></el-option>
              <el-option v-for="preset in configPresets" :key="preset.id" :value="preset.id" :label="preset.name"></el-option>
              <el-option value="..." label="[Create New]"></el-option>
          </el-select>
          <img src="../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" @click="deletePreset" v-if="selectedPreset"/>
        </div>
        <div class="configuration-dialog-molecule__dialog__selector" v-if="selectedPreset">
          <label>Name</label>
          <el-input size="small" v-model="selectedPreset.name"></el-input>
        </div>
        <div class="configuration-dialog-molecule__dialog__message" v-if="!selectedPreset">
          <h3>
            To configure this property you should select a configuration preset or create a new configuration preset. You can use the dropdown on the top.
          </h3>
        </div>
        <div class="configuration-dialog-molecule__dialog__body" v-if="selectedPreset">
          <div class="configuration-dialog-molecule__dialog__body__environments lc-custom-scroll">
            <div 
            :class="{'configuration-dialog-molecule__dialog__body__environments__env':env.id != selectedEnvironmentId, 'configuration-dialog-molecule__dialog__body__environments__env-selected':env.id == selectedEnvironmentId}" 
            v-for="env of environments" :key="env.id"
            @click="environmentSelected(env)"
            >
              <label>{{env.name}}</label>
            </div>
          </div>
          <div v-if="selectedPreset && configCategoryData" class="configuration-dialog-molecule__dialog__body__config" >
            <table v-if="selectedPreset.environments[selectedEnvironmentId]" class="configuration-dialog-molecule__dialog__body__config__table">
              <!-- <tr>
                <th>Key</th>
                <th>Value</th>
              </tr> -->
              <tr v-for="field of configCategoryData.staticFields" :key="field.name">
                <td class="key">
                  <label>{{field.name}}</label>
                </td>
                <td class="value">
                  <el-input v-model="selectedPreset.environments[selectedEnvironmentId][field.name]"></el-input>
                </td>
              </tr>
            </table>
          </div>
        </div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="visible = false">Cancel</el-button>
        <el-button type="primary" v-on:click="okClicked()">Ok</el-button>
      </span>

    </el-dialog>
  </div>
</template>

<script>
import ConfigurationDialogMolecule from './ConfigurationDialogMolecule.hook'
const hook = new ConfigurationDialogMolecule();

export default {
  name: 'ConfigurationDialogMolecule',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  beforeDestroy() { hook.unsubscribeGlobalEvents(); },
  methods: hook.methods("okClicked","dialogClosed","environmentSelected","dialogOpened","presetSelected","deletePreset"),
  computed: {
    environments(){ 
      return [{ "id":"*", "name" : "All Environments" }, ...(this.openedDocument?.environments || [])];
    },
    ...hook.getters("selectedItems","editorProfile","openedDocument")
  },
  mounted(){
    
  },
  data() {
      return { 
        selectedPresetId:null,
        selectedPreset:null,
        configPresets:[],
        selectedEnvironmentId:"*",
        configCategoryData:null,
        environmentConfig: null,
        visible: false,
        title:"Configuration editor",
        dialogBoxTop : `${(window.innerHeight /2) - 120}px`
      }
  }
}

</script>

<style scoped lang="scss">

.configuration-dialog-molecule {

  &__dialog {

    &__selector {
      display: flex;

      label {
        padding-top:10px;
        width:150px;
      }
      .el-select{
        width:100%;
      }

      .el-input {
        padding-right:30px;
      }

      img {
        margin-left:10px;
        margin-top:5px;
        margin-right:5px;
        height:20px;
        cursor: pointer;
      }

      margin-bottom:8px;
    }

    &__message{
      height:350px;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0.7;

      h3{
        margin:20px;
      }
    }

    &__body {
      margin-top:25px;
      display: flex;
      
      &__environments {
        overflow: auto;
        
        @mixin env-box(){
          height:50px;
          width:100%;
          
          display: table;
          cursor: pointer;

          > * {
            cursor: pointer;
            display: table-cell;
            vertical-align: middle;
          }

          label {
            padding-left:20px;
          }
        }

        &__env {
          @include env-box();
        }

        &__env-selected {
          @include env-box();
          background-color: rgb(70, 70, 70);
        }

        width:300px;
        height:300px;
        background-color: rgb(20, 20, 20);
      }

      &__config {
        margin-left:25px;
        width: calc(100% - 200px);
        background-color: rgb(20, 20, 20);

        &__table{
          padding:20px;

          tr{
            height:42px;

            th {
              text-align: left;
            }

            td.key {
              width:150px;
            }

            td.value {
              width:300px;
            }
          }
        }

      }
    }
  }

  .dialog-footer {
    .el-button {
      width:100px;
    }
  }

}

</style>
