import Strings from '../../UiResources/strings';
import {FrameworkElement} from '../../../Frontend/Core/FrameworkElement';
import {EVENTS} from '../../UiResources/events';

export default class AuthService extends FrameworkElement {

    info = {
        isLoggedIn:false,
        authData:null
    }

    constructor(){
        super();
        this.setBackendNamespace("UiServices").setElementName("AuthService");
    }

    getAuthToken(){
        return this.info.authData?.result?.token;
    }

    getProfile(){
        return this.info.authData?.result?.profile;
    }

    start(){
        this.addGlobalEvents();
        this.checkLocalStorage();
    }

    checkLocalStorage(){
        const localStorageItem = localStorage.getItem(Strings.CONSTANTS.LOCAL_STORAGE_AUTH);
        if (localStorageItem){
            this.info.isLoggedIn = true;
            this.info.authData = JSON.parse(localStorageItem);
            this.actions().setAuthData(this.info.authData);
        }
    }

    addGlobalEvents(){
        this.onGlobalEvent(EVENTS.AUTH.TOKEN_CREATED,this.onTokenCreated);
        this.onGlobalEvent(EVENTS.AUTH.TOKEN_ERROR,this.onAuthError);
        this.onGlobalEvent(EVENTS.AUTH.TOKEN_REVOKED,this.onTokenRevoked);
    }

    onTokenCreated(authData){
        localStorage.setItem(Strings.CONSTANTS.LOCAL_STORAGE_AUTH,JSON.stringify(authData));
        this.info.isLoggedIn = true;
        this.info.authData = authData;
        this.actions().setAuthData(authData);
    }

    onAuthError(){
        this.info.isLoggedIn = false;
        this.info.authData = null;
        localStorage.removeItem(Strings.CONSTANTS.LOCAL_STORAGE_AUTH);
        this.actions().setAuthData(this.actions().setAuthData({result:null,error:null}));
        location.reload();
    }

    onTokenRevoked(){
        localStorage.removeItem(Strings.CONSTANTS.LOCAL_STORAGE_AUTH);
        window.location.href = `/#/`;
    }

    async signOut(){
        const {error} =  await this.backend(Strings.PROGRESS_INDICATOR.SIGN_OUT).signOut();
        if (!error){
            Object.keys(localStorage).forEach(key=>{
                if (key.startsWith("store:") || key.startsWith("static-cache")){
                    localStorage.removeItem(key);
                }
            });

            this.emitGlobalEvent(EVENTS.AUTH.TOKEN_REVOKED);
        }
    }

    async signIn({email,password}){
        const signInData =  await this.backend(null).signIn(email,password);
        if (!signInData?.result?.error){
            this.updateAuthToken(signInData);
        }
        return signInData.result;
    }

    async signUp({host,recaptchaToken,email,password}){
        const signUpData =  await this.backend(null).signUp(host,recaptchaToken,email,password);
        this.updateAuthToken(signUpData);
    }

    async forgetPassword({email}){
        await this.backend(null).forgotPassword(email);
    }

    updateAuthToken(response){
        const {error} = response;
        if (!error){
            this.emitGlobalEvent(EVENTS.AUTH.TOKEN_CREATED,response);
            this.vue.$router.push('/');
        }
    }

}