exports.dynamicFieldsToObject  = (fields) => {
    let outObj = {};
    for (const field of fields){
        outObj[field.key] = field.value;
    }
    return outObj;
}

exports.getStaticCacheKey = (pageName, body) => {
    const params = body.params.join(",");
    return `${body.namespace}.${pageName}.${body.method}${params? "(" + params + ")" : ""}.json`.split("/").join("_");
}

exports.simpalizeFirstLetter = (str) => {
    if (str){
        return str[0].toLowerCase() + str.substring(1);
    }
    return str;
}

exports.capitalizeFirstLetter = (str) => {
    if (str){
        return str[0].toUpperCase() + str.substring(1);
    }
    return str;
}