<template>
  <div class="left-bar-molecule">
    <ul v-if="editorProfile">
      <li v-for="button in editorProfile.leftBar" :key="button.id">
        <el-tooltip class="item" effect="dark" :content="button.toolTip" placement="right">
          <router-link :to="button.route"> <img :src="getToolbarIcon(button.icon)"/> </router-link>
        </el-tooltip>
      </li>
    </ul>
  </div>
</template>

<script>
import LeftBarMolecule from './LeftBarMolecule.hook'
const hook = new LeftBarMolecule();

export default {
  name: 'LeftBarMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("getToolbarIcon"),
  props: { },
  data() {
      return { }
  },
  computed: hook.getters("editorProfile")
  
}

</script>

<style scoped lang="scss">

.left-bar-molecule {

    ul{
      list-style-type:none;
      padding-left:15px;
      li {
        cursor: pointer;
        margin-bottom:10px;
        img {
          height:35px;
          transition: 0.3s;
        }

        img:hover {
          opacity: 0.5;
        }
      }
    }

}

</style>
