import JsonRpcClient from "./JsonRpcClient";
import StaticCacheClient from "./StaticCacheClient";
import Errors from "../../Common/Errorcodes"
import {EVENTS} from '../../DesignSystem/UiResources/events';
import Strings from '../../DesignSystem/UiResources/strings';

export class FrameworkElement {
    vue;
    backendProxyCache = {};
    actionProxyCache = {};
    namespace = "Pages";
    elementName;
    actionProxy;
    eventHandlers = {};

    setElementName (elementName){
        this.elementName = elementName;
        return this;
    }

    setBackendNamespace(namespace){
        this.namespace = namespace;
        return this;
    }

    onGlobalEvent(event,callback){
        if (!this.eventHandlers[event]){
            this.eventHandlers[event] = [];
        }

        const callbackWrapper = (function(callback,self){
            return function(){
                const args = Array.from(arguments);
                callback.call(self, ...args);
            }
        })(callback,this);

        this.eventHandlers[event].push(callbackWrapper);

        this.vue.$uiFramework.eventBus.$on(event,callbackWrapper);
        return callbackWrapper;
    }

    unsubscribeGlobalEvents(){
        for (const eventName in this.eventHandlers){
            for (const eventHandler of this.eventHandlers[eventName]){
                this.vue.$uiFramework.eventBus.$off(eventName,eventHandler);
            }
        }
    }

    emitGlobalEvent(event,data){
        this.vue.$uiFramework.eventBus.$emit(event,data);
    }

    backend (options){
        let busyTitle;

        if (!options){
            busyTitle = Strings.PROGRESS_INDICATOR.PLEASE_WAIT;
            options = {};
        }else {
            if (typeof(options) === "string"){
                busyTitle = options;
                options = {};
            }else {
                busyTitle = options.busyTitle || Strings.PROGRESS_INDICATOR.PLEASE_WAIT;
            }
        }

        let pageName = options?.pageName || this.elementName;
        if (!busyTitle){
            busyTitle = options.busyTitle;
        }

        if (!this.backendProxyCache[pageName]){
            this.backendProxyCache[pageName] = (function(self){
                return new Proxy({}, {
                    get: (obj,methodName) =>{
                        return function(){               
                            return new Promise((resolve)=>{
                                if (busyTitle){
                                    self.startBusyIndicator(busyTitle);
                                }
                                
                                const args = Array.from(arguments);
                                self.callService(self,resolve,methodName,args,options);
                            })
                        }
                    }
                });
            })(this);            
        }
        
        return this.backendProxyCache[pageName];
    }

    async callService(self, resolve, methodName, args, options){

        let pageName = options?.pageName || this.elementName;
        let namespace = options?.namespace || this.namespace;
        let staticCache = options.staticCache || false;

        if (staticCache){
            const client = new StaticCacheClient(namespace,pageName, methodName, args);
            if (client.isEnabled()){
                let useLocalStorage = options.localStorage || false;
                let localStorageKey
                if (useLocalStorage){
                    localStorageKey = client.getLocalStorageKey();
                    const localStorageItem = localStorage.getItem(localStorageKey);
                    if (localStorageItem){
                        const result = JSON.parse(localStorageItem);
                        self.stopBusyIndicator();
                        self.resolveResult(self,resolve,result, options);
                        return;
                    }
                }
                
                const result = await client.call();
   
                if (!result.error){
                    self.stopBusyIndicator();
                    if (useLocalStorage){
                        localStorage.setItem(localStorageKey, JSON.stringify(result));
                    }
                    self.resolveResult(self,resolve,result, options);    
                    return;
                }
            }
        }

        const client = new JsonRpcClient(namespace);
        const token = self.vue.$uiFramework.services.AuthService.getAuthToken();
        const result = await client.call(token,pageName, methodName, args);
        self.stopBusyIndicator();

        if (result.error?.code === Errors.AUTH_ERROR){
            self.emitGlobalEvent(EVENTS.AUTH.TOKEN_ERROR,{});
        }else {
            self.resolveResult(self,resolve,result, options);
        }
    }

    resolveResult(self, resolve,result,options){
        let actions= options?.actions || null;

        resolve(result);
        if (actions){
            for(const action of actions){
                self.actions()[action](result);
            }
        }
    }

    hook (vue){
        this.vue = vue;
    }
          
    actions(vue = this.vue){
        if (!this.actionProxy){
            this.actionProxy = (function(vue){
                return new Proxy({}, {
                    get: (obj,attribName) =>{    
                        return function(){    
                            const args = Array.from(arguments);
                            vue.$store.dispatch(attribName, ...args);
                        }
                    }
                })
            })(vue);   
        }

        return this.actionProxy;
    }

    services (serviceName){
        return this.vue.$uiFramework.services[serviceName];
    }

    startBusyIndicator(caption, data={}){
        this.emitGlobalEvent(EVENTS.SHOW_BUSY_INDICATOR, {caption,...data});
    }

    stopBusyIndicator(){
        this.emitGlobalEvent(EVENTS.SHOW_BUSY_INDICATOR, null);
    }
}