import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';

export default class TopBarMolecule extends AtomicDesign {
    
    handleToolbarClick({...button}){
        if (button.event){
            this.emitGlobalEvent(button.event);
        }else {
            if (button.handler){
                button.handler();
            }
        }
    }

    async signOut(){
        const result = await this.msgbox("Do you want to sign out?", this.MSGBOX_TYPE.YES_NO, "Sign out");
        if (result == this.MSGBOX_BUTTONS.YES){
            this.services("AuthService").signOut();
        }        
    }

    handleCommand(command){
        switch(command){
            case "profile":
                this.emitGlobalEvent(EVENTS.TOOLBAR.SAVE, {callback:()=>{
                    this.actions().setExecutionFlow(null);
                    this.actions().setCompileErrors(null);
                    window.location.href = "/#/account";    
                }});
                break;
            case "signOut":
                this.signOut();
                break;
        }
    }

}