import Vue from 'vue'
import Environment from "../Environment/Environment"
import Errors from "../../Common/Errorcodes"
import Strings from "../../DesignSystem/UiResources/strings"
import {getStaticCacheKey} from '../../Common/Helpers';

export default class JsonRpcClient {

    pageName;
    request;

    constructor(namespace, pageName, method, params){
        this.request = {
            jsonrpc: "2.0", 
            namespace,
            method, 
            params, 
            id: 0
        };

        this.pageName = pageName;
    }

    isEnabled(){
        return Environment.staticCacheEndpoint ? true : false;
    }

    getLocalStorageKey (){
        return `static-cache:${getStaticCacheKey(this.pageName,this.request)}`;
    }

    checkVersion(){
        //Object.keys(localStorage)
    }

    async call(){

        const endpoint = `${Environment.staticCacheEndpoint}/${getStaticCacheKey(this.pageName,this.request)}`;

        try {
            const response = await Vue.axios.get(endpoint);
            return response.data;
        }catch (e){
            if (!e.response){
                return {error : {code:Errors.RPC_UNABLE_TO_CONNECT, message: `${Strings.RPC_UNABLE_TO_CONNECT} : ${endpoint}`}};
            }else {
                return {error : {code:Errors.RPC_UNKNOWN_ERROR, message: Strings.RPC_UNKNOWN_ERROR}};
            }  
        }
    }
}