<template>
  <div id="app" v-cloak>
    <DesktopContainerOrganism v-if="showIde === false"/>
    <EditorContainerOrganism v-if="showIde === true"/>
    <CloakMolecule v-if="showIde === undefined"/>
  </div>
</template>

<script>

import DesktopContainerOrganism from '../DesignSystem/UiContainers/DesktopContainerOrganism/DesktopContainerOrganism';
import EditorContainerOrganism from '../DesignSystem/UiContainers/EditorContainerOrganism/EditorContainerOrganism';
import CloakMolecule from '../DesignSystem/UiCommon/CloakMolecule/CloakMolecule';

export default {
  name: 'App',
  components: {
    DesktopContainerOrganism,
    EditorContainerOrganism,
    CloakMolecule
  },
  watch:{
      $route (to){ this.checkRoute(to.path); }
  },
  methods: {
    checkRoute(path){ this.showIde = path ? !path.includes("/login") : false;  }
  },
  data(){
    return {
      showIde:undefined
    }
  }
}
</script>

<style lang="scss"> 
  @import url("../DesignSystem/main.scss");
</style>
