<template>
  <div id="editor-container-organism">
    <SelectTemplateDialogMolecule/>
    <ConfigurationDialogMolecule/>
    <ObjectConstructorDialogMolecule/>
    <MsgboxMolecule :inputs="msgboxData"/>
    <ProgressIndicatorMolecule />
    <RunDialogOrganism/>
    <LogViewerDialogMolecule/>
    
    
    <div id="editor-container-organism__topbar">
      <TopBarMolecule/>
    </div>
    <div id="editor-container-organism__container">
        
        <transition name="left-bar">
          <div id="editor-container-organism__container__leftbar">
              <LeftBarMolecule />
          </div>
        </transition>
        <div id="editor-container-organism__container__container">
          <router-view/>
        </div>
    </div>
    <div id="editor-container-organism__bottombar">
      <BottomBarMolecule/>
    </div>
  </div>
</template>

<script>

import EditorContainerOrganism from './EditorContainerOrganism.hook'

import SelectTemplateDialogMolecule from './DialogBoxes/SelectTemplateDialogMolecule/SelectTemplateDialogMolecule';
import ConfigurationDialogMolecule from './DialogBoxes/ConfigurationDialogMolecule/ConfigurationDialogMolecule';
import ObjectConstructorDialogMolecule from './DialogBoxes/ObjectConstructorDialogMolecule/ObjectConstructorDialogMolecule';
import RunDialogOrganism from './DialogBoxes/RunDialogOrganism/RunDialogOrganism';
import LogViewerDialogMolecule from './DialogBoxes/LogViewerDialogMolecule/LogViewerDialogMolecule';

import BottomBarMolecule from './StructureElements/BottomBarMolecule/BottomBarMolecule';
import TopBarMolecule from './StructureElements/TopBarMolecule/TopBarMolecule';
import LeftBarMolecule from './StructureElements/LeftBarMolecule/LeftBarMolecule';
import MsgboxMolecule from './StructureElements/MsgboxMolecule/MsgboxMolecule';
import ProgressIndicatorMolecule from './StructureElements/ProgressIndicatorMolecule/ProgressIndicatorMolecule';

const hook = new EditorContainerOrganism();

export default {
  name: 'EditorContainerOrganism',
  beforeCreate() { 
    hook.hook(this);
    hook.addEventHandlers();
  },
  beforeDestroy(){ hook.unsubscribeGlobalEvents();  },
  methods: {},
  props: { },
  components:{
    TopBarMolecule,
    LeftBarMolecule,
    BottomBarMolecule,
    MsgboxMolecule,
    SelectTemplateDialogMolecule,
    ConfigurationDialogMolecule,
    ObjectConstructorDialogMolecule,
    ProgressIndicatorMolecule,
    RunDialogOrganism,
    LogViewerDialogMolecule
  },
  data() {
      return { 
        msgboxData: null
      }
  },
  watch:{}

}

</script>

<style scoped lang="scss">

div#editor-container-organism {
  user-select: none;
  height: 100vh;
  width:100vw;

  .left-bar-enter-active, .left-bar-leave-active {
    transition: opacity .3s, transform .3s;
  }

  .left-bar-enter, .left-bar-leave-to {
    opacity: 0;
  }

  &__topbar{
    background-color:#333333;
    height:40px;
  }

  &__container {
    display: flex;
    flex-direction: row;
    height: calc(100vh - 60px);

    &__leftbar{
      background-color:#333333;
      min-width:60px;
    }

    &__container{
      width: 100%;
    }
  }

  &__bottombar{
    background-color:#4D155F;
    height:20px;
  }


}

</style>
