import {FrameworkElement} from '../../../Frontend/Core/FrameworkElement';

export default class TemplateService extends FrameworkElement {

    constructor(){
        super();
        this.setBackendNamespace("UiServices").setElementName("TemplateService");
    }

    start(){

    }

    async getRenderedTemplate(data,json=false){
        const {error,result}  = await this.backend().getRenderedTemplate(data);
        return error ? null : (json ? JSON.parse(result) : result);
    }

}