<template>
  <div class="top-bar-molecule">
    <div class="top-bar-molecule__image">
      <router-link to="/"><img class="top-bar-molecule__image__logo" src="../../../../../Frontend/public/assets/toolbar_logo.png"/></router-link>   
    </div>
    <div class="top-bar-molecule__items">
      <transition name="fade">
        <ul class="top-bar-molecule__items__toolbar" v-if="topToolbar">
          <li v-for="button in topToolbar" :key="button.id">
            <el-tooltip class="item" effect="dark" :content="button.toolTip" placement="bottom">
              <img :src="getToolbarIcon(button.icon)" v-on:click="handleToolbarClick(button)"/>
            </el-tooltip>
          </li>
        </ul>
      </transition>
    </div>
    <div class="top-bar-molecule__avatar">

      <el-dropdown @command="handleCommand">
        <el-avatar :size="26" icon="el-icon-user-solid" :src="authData.profile.pic"></el-avatar>
        <el-dropdown-menu slot="dropdown" :hide-on-click="true" :split-button="true">        
          <el-dropdown-item command="profile">Edit Profile</el-dropdown-item>
          <el-dropdown-item command="signOut">Sign out</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      
    </div>
  </div>
</template>

<script>
import TopBarMolecule from './TopBarMolecule.hook'
const hook = new TopBarMolecule();

export default {
  name: 'TopBarMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("getToolbarIcon","handleToolbarClick","signOut", "editProfile","handleCommand"),
  props: { },
  data() {
      return { 
        
      }
  },
  computed: hook.getters("topToolbar","authData")
}

</script>

<style scoped lang="scss">

.top-bar-molecule {
  display: flex;
  flex-direction:row;
  
  &__image {
    padding-top:6px;
    &__logo{
      margin-left:20px;
      display: block;
      height:25px;
    }
  }

  &__avatar {
    cursor: pointer;
    padding-top:6px;
    padding-right: 10px;
    width:40px;
    text-align: right;
  }

  &__items{
      width: calc(100vw - 50px);

      .fade-enter-active, .fade-leave-active {
        transition: opacity .3s;
      }

      .fade-enter, .fade-leave-to {
        opacity: 0;
      }

      display: flex;
      align-items: center;
      flex-direction: row;
      height:100%;
  
      &__toolbar{
        list-style-type:none;
        margin:0px;
        padding-top:7px;
        li{
          display: inline;
          margin-right:20px;
          cursor: pointer;
          img {
            height:25px;
            transition: 0.3s;
          }
          img:hover {
            opacity:0.5;
          }
        }    
      }
  }
  
}

</style>
