import Vue from 'vue';
import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';
import {uuid} from '../../../../../Frontend/Helpers/uuid';

export default class ConfigurationMolecule extends AtomicDesign {

    isOkClicked=false;
    dialogBoxData=null;

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.DIALOG.SHOW_CONFIGURATION,this.onShow);
    }

    onShow(data){
        this.dialogBoxData = data;
        this.isOkClicked = false;
        this.vue.visible = true;
        this.dialogOpened();
    }

    dialogOpened(){
        const selectedField = this.dialogBoxData.field;
        const editorProfile = this.vue.editorProfile;
        const configCategory = selectedField?.typeData?.configCategory
        
        let presetsOfTheSameCategory = this.vue.openedDocument.settings.filter(preset=>preset.type == configCategory);
        let selectedPreset = {};
        
        if (presetsOfTheSameCategory.length == 0 ){
            const defaultPreset = this.createConfigEntry("Default",configCategory);
            presetsOfTheSameCategory.push(defaultPreset);
            this.vue.openedDocument.settings.push(defaultPreset);
            selectedPreset = {id: defaultPreset.id};
        }else {
            selectedPreset = this.dialogBoxData?.value;
        }
        this.vue.configPresets = presetsOfTheSameCategory;

        let selectedPresetObj = this.vue.configPresets.find(preset => preset.id == selectedPreset?.id);
        const configCategoryData = editorProfile.configTemplates.find(template=>template.type == configCategory);

        this.vue.selectedEnvironmentId = "*";
        this.vue.configCategoryData = configCategoryData;

        if (selectedPresetObj){
            for (const env of this.vue.openedDocument.environments){
                if (!selectedPresetObj.environments[env.id]){
                    this.vue.$set(selectedPresetObj.environments, env.id,{});
                }
            }

            this.vue.selectedPresetId = selectedPreset.id;
            this.vue.selectedPreset = selectedPresetObj;

        }else {
            this.vue.selectedPresetId = null;
            this.vue.selectedPreset = null;
        }
    }

    createConfigEntry(name,type){
        return Vue.observable({
            id: uuid(),
            name,
            type,
            environments : {
                "*" : {},
                "dev" : {} //create default development sandbox environment
            }
        })
    }

    dialogClosed(){
        if (!this.isOkClicked){
            if (this.dialogBoxData.callback){
                this.dialogBoxData.callback(null);
            }
        }

        this.dialogBoxData = null;
        this.vue.selectedPreset = null;
        this.vue.configPresets = [];
        this.vue.selectedEnvironmentId = "*";
        this.vue.configCategoryData = null;
        this.vue.environmentConfig = null;
    }

    okClicked(){
        this.isOkClicked = true;
        this.updateDocumentConfig();
        const selectedPreset = this.vue.selectedPreset;
        if (this.dialogBoxData.callback){
            this.dialogBoxData.callback({
                id: selectedPreset.id,
                name: selectedPreset.name
            });
        }
        this.vue.visible = false;
    }

    async deletePreset(){
        const result = await this.msgbox("Are you sure to delete this preset?", this.MSGBOX_TYPE.YES_NO, "Delete preset");

        if(result == this.MSGBOX_BUTTONS.YES) {
            const presetIndex = this.vue.openedDocument.settings.findIndex(setting => setting.id == this.vue.selectedPresetId);
            if (presetIndex !== -1){
                this.vue.openedDocument.settings.splice(presetIndex, 1);
            }
            this.vue.selectedPresetId = null;
        }
        
    }
    
    updateDocumentConfig(){
        const openedConfig = this.vue.openedDocument.settings;

        for (const preset of this.vue.configPresets){
            const index = openedConfig.findIndex(c=>c.id == preset.id);
            if (index !== -1){
                openedConfig.splice(index,1);
            }
        }

        for (const preset of this.vue.configPresets){
            openedConfig.push(preset);
        }
    }

    environmentSelected(env){
        this.vue.selectedEnvironmentId = env.id;
    }

    async presetSelected(presetId){
        if (presetId == "..."){

            this.vue.selectedPreset = null;
            this.vue.selectedPresetId = null;

            try {
                const {value} = await this.vue.$prompt('New preset name', 'New preset', { 
                    confirmButtonText: 'OK',
                    cancelButtonText: 'Cancel',
                });
                if (value){
                    
                    const selectedField = this.dialogBoxData.field;
                    const configCategory = selectedField?.typeData?.configCategory
                    const newPreset = this.createConfigEntry(value,configCategory);
                    
                    for (const env of this.vue.openedDocument.environments){
                        if (!newPreset.environments[env.id]){
                            newPreset.environments[env.id] = {};
                        }
                    }

                    this.vue.openedDocument.settings.push(newPreset);
                    this.vue.configPresets.push(newPreset);

                    this.vue.selectedPreset = this.vue.configPresets.find(preset => preset.id == newPreset.id);
                    this.vue.selectedPresetId = newPreset.id;
                }
    
            }catch (e){
                //input box is cancelled
            }

        }else {
            this.vue.selectedPreset = this.vue.configPresets.find(preset => preset.id == presetId);
        }
    }
}