import { mapGetters } from 'vuex';
import {FrameworkElement} from './FrameworkElement';
import {EVENTS} from '../../DesignSystem/UiResources/events';

export class AtomicDesign extends FrameworkElement {

    MSGBOX_BUTTONS = {
        CANCEL:1,
        OK:2,
        YES:3,
        NO:4
    };

    MSGBOX_TYPE = {
        OK:1,
        OK_CANCEL:2,
        YES_NO:3,
        YES_NO_CANCEL:4
    }

    methods(...methods){
        let methodStub = {};
        let self = this;
        for(let method of methods){
            methodStub[method] = (function(self){
                return function(){
                    const args = Array.from(arguments);
                    return self[method].call(self,...args);
                }
            })(self)
        }

        return methodStub;
    }

    getters(...getters){
        return {...mapGetters(getters)};
    }

    clearToolBar(){
        this.actions().setTopToolbar(null);
    }

    getToolbarIcon(icon) {
        var images = require.context('../public/assets/toolbar/');
        return images(`./${icon}`);
    }

    getAssetImage(image) {
        var images = require.context('../public/assets/images/');
        return images(`./${image}`);
    }

    msgbox(message,typeCode,title){
        return new Promise((resolve)=>{
            this.emitGlobalEvent(EVENTS.SHOW_MSGBOX,{
                message,
                typeCode,
                title,
                visible:true,
                callback: (result)=>{
                    this.emitGlobalEvent(EVENTS.SHOW_MSGBOX,null);
                    resolve(result);
                }
            });
        });
    }

}