import Vue from 'vue';
import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';
import {uuid} from '../../../../../Frontend/Helpers/uuid';

const configCategory = "objectConstructor";

export default class ObjectConstructorDialogMolecule extends AtomicDesign {

    isOkClicked=false;
    dialogBoxData=null;

    addEventHandlers(){
        this.onGlobalEvent(EVENTS.DIALOG.SHOW_OBJECT_CONSTRUCTOR,this.onShow);
    }

    onShow(data){
        this.dialogBoxData = data;
        this.isOkClicked = false;
        this.vue.visible = true;
        
        const schemaField = data.field.typeData.schemaRefField;
        const schemaId = data.selectedBlockProperties[schemaField];

        if (schemaId){
            this.vue.schemaId = schemaId           
            const presetId = data?.value?.id;
            this.vue.selectedPresetId = presetId;
        }else {
            this.msgbox("You must select a schema before you define the response structure", this.MSGBOX_TYPE.OK,"Schema not selected");
            this.vue.visible = false;
        }
    }



    createPresetEntry(name){
        const newPreset = Vue.observable({
            id: uuid(),
            name,
            type:configCategory,
            environments : {
                "*" : {},
            }
        });

        this.vue.selectVisible = false;
        this.vue.openedDocument.settings.push(newPreset);
        
        Vue.nextTick(()=> {
            this.vue.selectedPresetId = newPreset.id;
            this.vue.selectVisible = true;
        });

        return newPreset;
    }

    dialogClosed(){
        if (!this.isOkClicked){
            this.dialogBoxData.callback(null);
        }

        this.dialogBoxData = null;
        this.vue.selectedPresetId = null;
        this.vue.schemaId = null;
        this.selectedEntityId = null;
        this.selectedEntity = null;
    }

    okClicked(){
        this.isOkClicked = true;
        const selectedPreset = this.vue.selectedPreset;
        this.dialogBoxData.callback({
            id: selectedPreset.id,
            name: selectedPreset.name
        });
        this.vue.visible = false;
    }

    async deletePreset(){
        const result = await this.msgbox("Are you sure to delete this preset?", this.MSGBOX_TYPE.YES_NO, "Delete preset");

        if(result == this.MSGBOX_BUTTONS.YES) {
            const presetIndex = this.vue.openedDocument.settings.findIndex(setting => setting.id == this.vue.selectedPresetId);
            if (presetIndex !== -1){
                this.vue.openedDocument.settings.splice(presetIndex, 1);
            }
            this.vue.selectedPresetId = null;
        }
        
    }

    async presetSelected(presetId){
        if (presetId == "..."){

            this.vue.selectedPresetId = null;

            try {
                const {value} = await this.vue.$prompt('New preset name', 'New preset', { 
                    confirmButtonText: 'OK',
                    cancelButtonText: 'Cancel',
                });
                if (value){
                    this.createPresetEntry(value);
                }
    
            }catch (e){
                //input box is cancelled
            }

        }else {
            this.vue.selectedPresetId = presetId;
        }
    }
}