const navigateToHome  = (vue,next) => {
  const isLoggedIn = vue.$uiFramework.services?.AuthService?.info?.isLoggedIn;
  if (isLoggedIn){
    next();
  }else {
    next("/login");
  }
};

const authGuard = (vue, to, from, next) => {
  
  if (vue.$uiFramework.isInitialized){
    navigateToHome(vue,next);
  }else {
    vue.$uiFramework.onInitialized(()=>{
      navigateToHome(vue,next);
      vue.$uiFramework.onInitialized(null);
    });
  }
};


const lazyLoad = (subfolder,pageName)=> import(`.${subfolder}/${pageName}/${pageName}.vue`);
export const createDefault = (vue, path, name, title,subfolder="",isAuth=true) => {
  return {
    path,
    name,
    component : ()=> lazyLoad(subfolder,name),
    meta: {title: title},
    beforeEnter: (to,from,next) => isAuth ? authGuard(vue, to,from,next) : next()
  };
}

