import { LEFT_TOOLBAR_BUTTONS} from '../../DesignSystem/UiResources/leftToolbar';
import { TOP_TOOLBAR_BUTTONS } from '../../DesignSystem/UiResources/topToolbar';

export const extractToolbarButtons = (baseObj, buttons) => { 
    let objects = [];
    for(let button of buttons){
        let buttonObj = baseObj[button];
        if(buttonObj){
            objects.push({...buttonObj});
        }
    }
    return objects;
};

export const getLeftToolbarButtons = (buttons) => extractToolbarButtons(LEFT_TOOLBAR_BUTTONS, buttons);
export const getTopToolbarButtons = (...buttons) => extractToolbarButtons(TOP_TOOLBAR_BUTTONS, buttons);