<template>
  <div class="progress-indicator-molecule" v-if="data">
    <img src="../../../../../Frontend/public/assets/loader/busy.gif" />
    <label class="progress-indicator-molecule__caption">{{data.caption}}</label>
  </div>
</template>

<script>
import ProgressIndicatorMolecule from './ProgressIndicatorMolecule.hook'
const hook = new ProgressIndicatorMolecule();

export default {
  name: 'ProgressIndicatorMolecule',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  beforeDestroy() { hook.unsubscribeGlobalEvents(); },
  methods: { },
  props: { },
  data() {
      return { 
        data: null
      }
  }
}

</script>

<style scoped lang="scss">

.progress-indicator-molecule {
  width:400px;
  height:120px;
  position: absolute;
  left: 50%;
  top:50%;
  margin-left:-200px;
  margin-top:-70px;
  background-color: rgb(0, 0, 0);
  border-radius: 15px;
  opacity: 0.8;
  z-index: 2200;
  text-align: center;
  align-content: center;
  align-items: center;
  
  img {
    height:60px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-bottom:15px;
    margin-top:10px;
  }

  label {
    display: block;
  }
}

</style>
