import {FrameworkElement} from '../../../Frontend/Core/FrameworkElement';
import {EVENTS} from '../../UiResources/events';

export default class HotKeyService extends FrameworkElement {


    pressedModifierKeys = {
        ctrl:false,
        alt:false,
    }

    hotKeys = [];

    constructor(){
        super();
    }

    start(){
        this.mapHotKeys();
        this.addKeyboardEvents();
    }

    addKeyboardEvents(){       
        document.onkeyup= (e)=>{
            this.checkModifierKey(e.keyCode,false);
        }
    
        document.onkeydown = (e) => {
            const isModifierKeyPressed = this.checkModifierKey(e.keyCode,true);
            if (!isModifierKeyPressed){
                return this.handleHotKeys(e.keyCode);
            }
            
        }
    }

    checkModifierKey(keyCode, result){
        switch(keyCode){
            case 17: 
                this.pressedModifierKeys.ctrl = result;
                return true;
        }
        return false;
    }

    handleHotKeys(keyCode){
        const openedDocument = this.vue.$store.getters.openedDocument;
        if (openedDocument){
            const availableKeys = Object.keys(this.pressedModifierKeys);
            for (var hotKey of this.hotKeys){
                let isMapped = true;
    
                for (const key of availableKeys){
                    if (hotKey[key] != this.pressedModifierKeys[key]){
                        isMapped = false;
                        break;
                    }
                }
                if (isMapped){
                    if (keyCode == hotKey.keyCode.charCodeAt(0)){
                        hotKey.handler();
                        return false;
                    }
                }
            }
        }

        return true;
    }

    mapHotKeys(){
        this.hotKeys = [
            {
                ctrl:true,
                alt:false,
                keyCode: 'S',
                handler: () =>{
                    this.emitGlobalEvent(EVENTS.TOOLBAR.SAVE);
                }
            },
            // {
            //     ctrl:true,
            //     alt:false,
            //     keyCode: 'Z',
            //     handler: () =>{
            //         this.emitGlobalEvent(EVENTS.TOOLBAR.UNDO);
            //     }
            // },
            // {
            //     ctrl:true,
            //     alt:false,
            //     keyCode: 'Y',
            //     handler: () =>{
            //         this.emitGlobalEvent(EVENTS.TOOLBAR.REDO);
            //     }
            // },
            // {
            //     ctrl:true,
            //     alt:false,
            //     keyCode: 'X',
            //     handler: () =>{
            //         this.emitGlobalEvent(EVENTS.TOOLBAR.CUT);
            //     }
            // },
            // {
            //     ctrl:true,
            //     alt:false,
            //     keyCode: 'C',
            //     handler: () =>{
            //         this.emitGlobalEvent(EVENTS.TOOLBAR.COPY);
            //     }
            // },
            // {
            //     ctrl:true,
            //     alt:false,
            //     keyCode: 'V',
            //     handler: () =>{
            //         this.emitGlobalEvent(EVENTS.TOOLBAR.PASTE);
            //     }
            // }
        ];
    }

}