const getFromStorage  = (persist, stateName, initialState) =>{
    if (persist){
        const key = `store:${stateName}`;
        const data = localStorage.getItem(key);
        return data ? JSON.parse(data) : initialState;
    }else {
        return initialState;
    }
};

const modifyStorage = (persist, stateName, value) =>{
    if (persist){
        const key = `store:${stateName}`;
        if (value){
            localStorage.setItem(key, JSON.stringify(value));
        }else {
            localStorage.removeItem(key);
        }
    }
};

const getDefaultStateMutationsActions = (stateName,initialState,persist)=>{
    const actionSuffix = `${stateName.charAt(0).toUpperCase()}${stateName.slice(1)}`;
    const setActionName = `set${actionSuffix}`;
    const patchActionName = `patch${actionSuffix}`;

    let state = {};
    let mutations = {};
    let actions = {};
    
    state[stateName] = getFromStorage(persist,stateName, initialState);

    mutations[setActionName] = function(state,value){
        state[stateName] = value;
        modifyStorage(persist, stateName,value);
    }

    actions[setActionName] = function(context,value){
        context.commit(setActionName, value);
    }

    mutations[patchActionName] = function(state,value){
        state[stateName] = {
            ...state[stateName],
            ...value
        };
        modifyStorage(persist, stateName,value);
    }

    actions[patchActionName] = function(context,value){
        context.commit(patchActionName, value);
    }

    return {
        state,
        mutations,
        actions
    }
}

export function createDefaultRpcState(stateName, initialState,persist=false){
    const defaultData = getDefaultStateMutationsActions(stateName, initialState,persist);
    
    let getters = {};
    getters[stateName + "Error"] = state => state[stateName].error;
    getters[stateName + "Full"] = state => state[stateName];
    getters[stateName] = state => state[stateName].result;


    return {
        ...defaultData,
        getters
    }
}

export function createDefaultState(stateName, initialState,persist=false){   
    const defaultData = getDefaultStateMutationsActions(stateName, initialState,persist);
    
    let getters = {};
    getters[stateName] = state => state[stateName];

    return {
        ...defaultData,
        getters
    }
}