import Vue from 'vue';
import Services from '../../DesignSystem/UiServices';

class UiFramework {
    rootVue;

    eventBus = new Vue();
    services = {};
    isInitialized = false;
    events = {
        onInitialize:null
    };

    bootstrapOne(service){
        let serviceObj = new service();
        this.services[serviceObj.elementName] = serviceObj;
        serviceObj.hook(this.rootVue);
        serviceObj.start();
    }

    bootstrapServices(){
        Services.forEach(service => this.bootstrapOne(service));
        this.isInitialized = true;
        if(this.events.onInitialize){
            this.events.onInitialize();
        }
    }

    bootstrap(vue){
        this.rootVue = vue;
        this.bootstrapServices();
    }

    onInitialized(callback){
        this.events.onInitialize = callback;
    }
}

export default {
    install(vue) {
      vue.prototype.$uiFramework = new UiFramework();
    }
};
