<template>
  <div class="log-viewer-dialog-molecule">
    <el-dialog :title="title" :visible.sync="visible" width="1000px" top="60px">

      <div class="log-viewer-dialog-molecule__dialog lc-custom-scroll">
        <div v-for="log of logs" :key="log.id" class="log-viewer-dialog-molecule__dialog__log-line">
          <div :class="{'log-error' : log.type=='ERROR'}">
            <label>{{log.type}}</label>
            <label>{{log.message}}</label>
          </div>
        </div>
      </div>

      <span slot="footer" class="log-viewer-dialog-molecule__buttons">
        <el-button type="primary" @click="visible = false">Close</el-button>
      </span>

    </el-dialog>
    
  </div>
</template>

<script>
import LogViewerDialogMolecule from './LogViewerDialogMolecule.hook'
const hook = new LogViewerDialogMolecule();


export default {
  name: 'LogViewerDialogMolecule',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  beforeDestroy() { hook.unsubscribeGlobalEvents(); },
  methods: hook.methods("dialogClosed"),
  components: {
  },
  props: { },
  data() {
      return { 
        logs: [],
        title: "Log viewer",
        visible:false
      }
  }
}

</script>

<style scoped lang="scss">

.log-viewer-dialog-molecule {
  &__buttons {
    .el-button {
      width:100px;
    }
  }

  &__dialog {
    background-color: #1E1E1E;
    display: flex;
    flex-direction: column;
    height:250px;
    overflow: auto;
    padding:20px;
    
    &__log-line {
      padding-bottom:3px;
      .log-error {
        color: rgb(180, 115, 115);       
      }
      label {
        margin-right:10px;
      } 
    }
  }
}

</style>
