<template>
  <div class="run-dialog-organism">
    <el-dialog :title="title" :visible.sync="visible" width="800px" @close="dialogClosed" :close-on-click-modal="false" top="40px" >
      <div class="run-dialog-organism__dialog lc-custom-scroll">
        <div class="run-dialog-organism__dialog__selector">
            <label>Preset</label>
            <el-select size="small"
            placeholder=""
            v-model="selectedPresetId" :filterable="true"
            v-on:change="presetSelected(selectedPresetId)">
                <el-option :value="null" label=""></el-option>
                <el-option value="..." label="[Create New]"></el-option>
                <el-option v-for="preset in presets" :key="preset.id" :value="preset.id" :label="preset.name"></el-option>
            </el-select>
            <img src="../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" @click="deletePreset" v-if="selectedPreset"/>
        </div>

        <div class="run-dialog-organism__dialog__selector" v-if="selectedPreset">
          <label>Name</label>
          <el-input size="small" v-model="selectedPreset.name"></el-input>
        </div>

        <div class="run-dialog-organism__dialog__selector" v-if="selectedPreset">
            <label>Environment</label>
            <el-select size="small" placeholder="" v-model="selectedPreset.environmentId" :filterable="true" >
                <el-option v-for="env in openedDocument.environments" :key="env.id" :value="env.id" :label="env.name"></el-option>
            </el-select>
            <div class="blank"></div>
        </div>

        <div class="run-dialog-organism__dialog__body">
           <ApiRunMolecule v-if="selectedPreset" :selectedPreset="selectedPreset" :allHeaders="allHeaders"/>
           <div v-if="!selectedPreset" class="run-dialog-organism__dialog__body__message">
             <label>Please select or create a new preset to run</label>
           </div>
        </div>
      </div>

      <span slot="footer" class="run-dialog-organism__buttons">
        <el-button @click="visible = false">Cancel</el-button>
        <el-button type="primary" v-on:click="run()" :disabled="!selectedPresetId && !selectedEnvironment">Run</el-button>
      </span>
      

    </el-dialog>
    
  </div>
</template>

<script>
import ApiRunMolecule from './ApiRunMolecule/ApiRunMolecule';
import RunDialogOrganism from './RunDialogOrganism.hook'
const hook = new RunDialogOrganism();
hook.setBackendNamespace("UiContainers/EditorContainerOrganism/DialogBoxes").setElementName("RunDialogOrganism");

export default {
  name: 'RunDialogOrganism',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  beforeDestroy() { hook.unsubscribeGlobalEvents(); },
  methods: hook.methods("run","dialogClosed","presetSelected","deletePreset"),
  props: { },
  components: {
    ApiRunMolecule
  },
  data() {
      return { 
        selectedPresetId:null,
        visible: false,
        title:"Run endpoint",
        allHeaders: []
      }
  },
  computed: {
    selectedPreset(){
      return this.presets.find(preset => preset.id == this.selectedPresetId);
    },
    selectedEnvironment(){
      const envId = this.selectedPreset?.environmentId;
      if (envId){
        return this.openedDocument.environments.find(env=> env.id == envId);
      }
      return null;
    },
    presets(){
      const selectedEditable = this.selectedItems?.editable;
      if (!selectedEditable){
        return [];
      }
        
      return this.openedDocument?.settings?.filter(config => config.type == "run" && config.editableId == selectedEditable.id);
    },
    ...hook.getters("openedDocument","editorProfile","selectedItems")
  }
}

</script>

<style scoped lang="scss">

.run-dialog-organism {

  &__buttons {
    button {
      width:100px;
    }
  }

  &__dialog {
    overflow: auto;
    background-color: rgb(20, 20, 20);
    padding:10px;
    min-height:360px;

    &__selector{
      padding-left:5px;  
      height:40px;
      display: flex;
      align-items: center;


      label {
        width:200px;
      }

      .el-select {
        width:100%;
        margin-right:20px;
      }
      
      img {
        height:20px;
        cursor: pointer;
      }

      .el-input {
        padding-right:35px;
      }

      div.blank{
        height:20px;
        width:20px;
      }
    }

    &__body {
      margin-top:20px;

      &__message {
        height: 300px;
        width: 100%;
        display: flex;
        align-items: center;
        
        label{
          font-size: 20px;
          opacity: 0.5;
          width:100%;
          text-align: center;
        }
        
      }
    }
  }
}

</style>
