<template>
  <div class="json-editor-atom">
    <div class="json-editor-atom__filter" v-if="isArray">
      <el-popover placement="right" width="400" trigger="click" v-if="selectedConfig && entity">
        <table style="width:100%">
          <tr>
            <td class="key">
              <label>Where</label>
            </td>
            <td class="value">
              <el-input size="mini" v-model="selectedConfig[entity.id].filters.where"></el-input>
            </td>
          </tr>
          <tr>
            <td class="key">
              <label>Order By</label>
            </td>
            <td class="value">
              <el-input size="mini" v-model="selectedConfig[entity.id].filters.orderBy"></el-input>
            </td>
          </tr>
          <tr>
            <td class="key">
              <label>Skip</label>
            </td>
            <td class="value">
              <el-input size="mini" v-model="selectedConfig[entity.id].filters.skip"></el-input>
            </td>
          </tr>
          <tr>
            <td class="key">
              <label>Take</label>
            </td>
            <td class="value">
              <el-input size="mini" v-model="selectedConfig[entity.id].filters.take"></el-input>
            </td>
          </tr>
        </table>
        <el-button slot="reference" size="mini" v-if="isArray">
          <img src="../../../../../../Frontend/public/assets/images/filter-icon.svg"/>
          Filter '{{entity.name}}s'
        </el-button>
      </el-popover>
    </div>

    <p class="topBrackets" v-if="isArray">[{
    </p>
    <p class="topBrackets" v-if="!isArray">{</p>

    <ul class="json-editor-atom__ul" v-if="entity">

      <li v-for="field of entity.fields" :key="field.id" class="json-editor-atom__li">
        <div v-if="selectedConfig[entity.id]">
          <div v-if="selectedConfig[entity.id][field.id]" :class="{
            'json-editor-atom__ul__li__divlabel-selected': false,
            'json-editor-atom__ul__li__divlabel':  true
            }">
              <el-checkbox v-model="selectedConfig[entity.id][field.id].checked" size="mini"></el-checkbox>
              <label @click.stop="itemSelected" >{{field.name}}</label>
              <!-- <label v-if="!(typeof(field.dataType) ==='string' && field.isArray)" >{{field.name}}</label> -->
              <!-- <label v-if="typeof(field.dataType) ==='string' && field.isArray" @click.stop="itemSelected" style="text-decoration:underline;" >{{field.name}}</label>
              <label>:</label>
              <label v-if="typeof(field.dataType) !=='string'">
                <label v-if="field.isArray">[],</label>
                <label v-if="!field.isArray">
                  <label v-if="field.dataType === 'PARENTREF'">
                    
                  </label>
                  <label v-if="field.dataType !== 'PARENTREF'">
                    <label v-if="dataTypes[field.dataType].jsonType =='string'">""</label>
                    <label v-if="dataTypes[field.dataType].jsonType =='number'">0</label>
                  </label>
                  
                </label>
              </label> -->
              <label v-if="selectedConfig[entity.id][field.id].checked && typeof(field.dataType) ==='string'">
                <JsonEditorAtom :schemaInfo="schemaInfo" 
                :selectedConfig="selectedConfig[entity.id][field.id]" 
                :entity="getEntity(field)" 
                :isArray="field.isArray"
                :dataTypes="dataTypes"
                @itemSelected="itemSelectedChild" 
                />
              </label>
          </div>
        </div>
      </li>
    </ul>

    <p class="bottomBrackets" v-if="isArray">}]</p>
    <p class="bottomBrackets" v-if="!isArray">}</p>
  </div>
</template>

<script>
import JsonEditorAtomHook from './JsonEditorAtom.hook'
const hook = new JsonEditorAtomHook();

function fillEmptyFields(vue,selectedConfig){
  if (vue.entity){
    const entityId = vue.entity.id;

    if (!selectedConfig[entityId]){
      vue.$set(selectedConfig,entityId,{});
    }

    if (!selectedConfig[entityId].filters){
      vue.$set(selectedConfig[entityId],"filters",{checked:false});
    }

    const entity = vue.schemaInfo.entities.find(entity => entity.id == entityId);
    for(const field of entity.fields){
      if (!selectedConfig[entityId][field.id]){
        const checked = typeof(field.dataType) !== 'string';
        const obj = {checked};
        vue.$set(selectedConfig[entityId],field.id,obj);
      }
    }
  }
}

export default {
  name: 'JsonEditorAtom',
  beforeCreate() { hook.hook(this)},
  methods: {
    getEntity(field){
      return this.schemaInfo.entities.find(entity => entity.id == (field.entityId || field.dataType));
    },
    itemSelected(){
      this.$emit("itemSelected",this.selectedConfig[this.entity.id], this.isArray, this.entity);
    },
    itemSelectedChild(config,isArray){
      this.$emit("itemSelected",config,isArray);
    }
  },
  components: {
    JsonEditorAtom: this
  },
  mounted(){
    fillEmptyFields(this,this.selectedConfig);
  },
  watch:{
    selectedConfig(val){
      fillEmptyFields(this,val);
    }
  },
  props: { 
    isArray:Boolean,
    schemaInfo: Object,
    entity:Object,
    selectedConfig:Object,
    dataTypes: Object
  },
  data() {
      return {
        checked:true
      }
  }
}

</script>

<style scoped lang="scss">

.json-editor-atom {
  font-size:18px;

  &__filter{
    .el-button {
      margin-left:-1px;
      margin-top:5px;
      margin-bottom:5px;
    }

    img {
      height:10px;
    }

    // margin-bottom:15px;
  }



  label{
      display: block;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding-bottom:0px;
  }

  p.topBrackets {
    margin:0px;
  }

  p.bottomBrackets {
    margin:0px;
    margin-top:10px;
    margin-bottom:10px;
  }

  &__ul {
    list-style:none;

    color: rgb(202, 202, 202);
    padding-left:0px;
    margin:0px;
    &__li {

      @mixin divLabel(){
        
        transition: 0.25s;
        display: flex;
        align-items: top;

        label {
          cursor: pointer;
          margin-left:5px;
          margin-right:5px;

        }
        
        .el-checkbox {
          margin-right:10px;
        }
      }

      &__divlabel{
        @include divLabel();
      }

      &__divlabel-selected {
        @include divLabel();
        color: rgb(127, 219, 127);
      }

    }
  }

}

</style>
