import Router from 'vue-router'
import {routes, routerVue} from './Settings'
import Strings from '../../DesignSystem/UiResources/strings'

const router = new Router({routes});

router.beforeEach((to, from, next) => {
  const editorService = routerVue.$uiFramework.services?.EditorService;
  if (editorService){
    editorService.startProgressIndicator();
  }
    
  document.title = to?.meta?.title || Strings.DEFAULT_TITLE;
  next();
});

router.afterEach(() => {
  const editorService = routerVue.$uiFramework.services?.EditorService;
  if (editorService){
    editorService.stopProgressIndicator();
  }
})

export default router;

