import {AtomicDesign} from '../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../UiResources/events';

export default class EditorContainerOrganism extends AtomicDesign {
    
    addEventHandlers(){
        this.onGlobalEvent(EVENTS.SHOW_MSGBOX,this.showMsgbox);
    }

    showMsgbox(data){
        this.vue.msgboxData = data;
    }
}