import {FrameworkElement} from '../../../Frontend/Core/FrameworkElement';
import {EVENTS,REASONS} from '../../UiResources/events';
import Strings from '../../UiResources/strings';
import {uuid} from '../../../Frontend/Helpers';

export default class EditorService extends FrameworkElement {

    info = {
        isLoggedIn:false,
        authData:null
    }

    constructor(){
        super();
        this.setBackendNamespace("UiServices").setElementName("EditorService");
    }

    start(){
        this.addGlobalEvents();
    }

    addGlobalEvents(){
        this.onGlobalEvent(EVENTS.TOOLBAR.NEW_ITEM,this.newItem);
        this.onGlobalEvent(EVENTS.TOOLBAR.SAVE,this.save);
        this.onGlobalEvent(EVENTS.TOOLBAR.FULL_SCREEN,this.fullScreen);
        this.onGlobalEvent(EVENTS.TOOLBAR.VIEW_DEFAULT,this.switchToDefaultView);
        this.onGlobalEvent(EVENTS.TOOLBAR.VIEW_DETAILED,this.switchToDetailedView);
        this.onGlobalEvent(EVENTS.TOOLBAR.NEW_FOLDER,this.newFolder);
    }

    async newFolder(){
        try {
            const {value} = await this.vue.$prompt('Folder Name', 'New Folder', { 
                confirmButtonText: 'OK',
                cancelButtonText: 'Cancel',
            });
            if (value){
                const openedDocument = this.vue.$store.getters.openedDocument;
                const newFolder = {
                    id:uuid(),
                    name: value,
                    parentId:null,
                    collapsed:true
                };
                console.log(openedDocument);
                openedDocument.folders.push(newFolder);

                if (openedDocument.selectedResources.defaultView == "default"){
                    this.switchToDetailedView();
                }else {
                    this.actions().setOpenedDocument({result:openedDocument});
                }

                this.vue.$notify({type: 'success', message: `New folder '${value}' created`, position: 'bottom-right', offset:0 });
            }

        }catch (e){
            //input box is cancelled
        }
    }
    

    switchToDefaultView(){
        const openedDocument = this.vue.$store.getters.openedDocument;
        openedDocument.selectedResources.defaultView = "default";
        this.actions().setOpenedDocument({result:openedDocument});
    }

    switchToDetailedView(){
        const openedDocument = this.vue.$store.getters.openedDocument;
        openedDocument.selectedResources.defaultView = "detailed";
        this.actions().setOpenedDocument({result:openedDocument});
    }

    async newItem(){
        this.emitGlobalEvent(EVENTS.TEMPLATE.SHOW_DIALOG,{
            type:"event",
            title:"Please select a template for your new endpoint",
            callback : (data) => {
                if (data){
                    this.templateSelected(data)
                }
            }
        });
    }

    async templateSelected(data){
        const openedDocument = this.vue.$store.getters.openedDocument;
        const renderedObj = await this.services("TemplateService").getRenderedTemplate(data,true);
        openedDocument.editables.push(renderedObj);
        this.actions().setOpenedDocument({result:openedDocument});
    }

    async save(inputs){
        this.emitGlobalEvent(EVENTS.DOCUMENT.BEFORE_SAVE,{reason:REASONS.BEFORE_SAVE.USER_SAVE});
        const openedDocument = this.vue.$store.getters.openedDocument;
        const {error}  = await this.backend(Strings.PROGRESS_INDICATOR.SAVING).save(openedDocument);
        let result = true;
        if (!error){
            if (!inputs?.hideNotification){
                this.vue.$notify({type: 'success', message: 'Successfully saved', position: 'bottom-right', offset:0 });
            }
            
        }else {
            if (!inputs?.hideNotification){
                this.vue.$notify({type: 'error', message: 'Error saving', position: 'bottom-right' });
            }
            result = false;
        }

        if (inputs?.callback){
            inputs.callback(result);
        }

    }

    fullScreen(){
        const elem = document.getElementById("app");

        if(document.fullscreenElement){
            if (document.exitFullscreen) {
                document.exitFullscreen();
              } else if (document.mozCancelFullScreen) {
                document.mozCancelFullScreen();
              } else if (document.webkitExitFullscreen) {
                document.webkitExitFullscreen();
              } else if (document.msExitFullscreen) {
                document.msExitFullscreen();
              }
        }else {
            if (elem.requestFullscreen) {
                elem.requestFullscreen();
              } else if (elem.mozRequestFullScreen) { 
                elem.mozRequestFullScreen();
              } else if (elem.webkitRequestFullscreen) {
                elem.webkitRequestFullscreen();
              } else if (elem.msRequestFullscreen) {
                elem.msRequestFullscreen();
              }
        }
    }

    startProgressIndicator(){
        this.startBusyIndicator("Loading page");
    }   

    stopProgressIndicator(){
        this.stopBusyIndicator();
    }

    getAvailableVariables(editable){

        const method = editable?.properties['0'].method

        let userDefinedVars = [];
        
        const editableSpecificVars = [
            {name: "headers", type:"object", caption: "headers", description:"HTTP Headers"},
            {name: "pathParameters", type:"object", caption: "pathParameters", description:"Path Parameters"},
            {name: "queryParameters", type:"object", caption: "queryParameters", description:"Query Parameters"},
            {name: "input", type:"object", caption: "input", description:"All the variables"},
            {name: "context", type:"object", caption: "context", description:"All the variables"},
            {name: "last", type:"any", caption: "last", description:"Output from the previous block"},
            {name: "properties", type:"object", caption: "properties", description:"Properties of the current block"},
          ];
          
        if (method !== "GET"){
            editableSpecificVars.push({name: "post", type:"object", caption: "post", description:"POST Body JSON"});
            editableSpecificVars.push({name: "rawBody", type:"string", caption: "rawBody", description:"Unformatted POST Bidy"});
        }


        Object.keys(editable.properties).forEach(blockId=>{
            const blockProperties = editable.properties[blockId];
            
            Object.keys(blockProperties).forEach(propName=>{
                const propValue = blockProperties[propName];
                if (propValue?.variableName){
                    userDefinedVars.push({name:propValue.variableName, type: propValue.type})
                }
            });
            
        });

        return editableSpecificVars.concat(userDefinedVars);
    }

}