<template>
  <div class="msgbox-molecule">
    <el-dialog :title="title" :visible.sync="visible" width="500px" center :top="dialogBoxTop">
      <span>{{message}}</span>
      <span slot="footer" class="dialog-footer" v-if="typeCode == MSGBOX_TYPE.OK">
        <el-button type="primary" v-on:click="buttonClicked(MSGBOX_BUTTONS.OK)">{{okCaption}}</el-button>
      </span>
      <span slot="footer" class="dialog-footer" v-if="typeCode == MSGBOX_TYPE.OK_CANCEL">
        <el-button v-on:click="buttonClicked(MSGBOX_BUTTONS.CANCEL)">{{cancelCaption}}</el-button>
        <el-button type="primary" v-on:click="buttonClicked(MSGBOX_BUTTONS.OK)">{{okCaption}}</el-button>
      </span>
      <span slot="footer" class="dialog-footer" v-if="typeCode == MSGBOX_TYPE.YES_NO">
        <el-button v-on:click="buttonClicked(MSGBOX_BUTTONS.YES)">{{yesCaption}}</el-button>
        <el-button type="primary" v-on:click="buttonClicked(MSGBOX_BUTTONS.NO)">{{noCaption}}</el-button>
      </span>
      <span slot="footer" class="dialog-footer" v-if="typeCode == MSGBOX_TYPE.YES_NO_CANCEL">
        <el-button type="primary" v-on:click="buttonClicked(MSGBOX_BUTTONS.YES)">{{yesCaption}}</el-button>
        <el-button v-on:click="buttonClicked(MSGBOX_BUTTONS.NO)">{{noCaption}}</el-button>
        <el-button v-on:click="buttonClicked(MSGBOX_BUTTONS.CANCEL)">{{cancelCaption}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import MsgboxMolecule from './MsgboxMolecule.hook'
const hook = new MsgboxMolecule();

export default {
  name: 'MsgboxMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("buttonClicked"),
  computed:{
    visible(){ return this.inputs ? this.inputs.visible : false; },
    message(){ return this.inputs ? this.inputs.message : ""; },
    typeCode(){ return this.inputs ? this.inputs.typeCode : 1; },
    title(){ return this.inputs ? this.inputs.title : ""; },
    okCaption(){ return this.inputs?.captions?.ok || "Ok"; },
    cancelCaption(){ return this.inputs?.captions?.ok || "Cancel"; },
    yesCaption(){ return this.inputs?.captions?.ok || "Yes"; },
    noCaption(){ return this.inputs?.captions?.ok || "No"; },
  },
  props: { 
    inputs: null
  },
  data() {
      return { 
        MSGBOX_BUTTONS: hook.MSGBOX_BUTTONS,
        MSGBOX_TYPE: hook.MSGBOX_TYPE,
        dialogBoxTop : `${(window.innerHeight /2) - 120}px`
      }
  }
}

</script>

<style scoped lang="scss">

.msgbox-molecule {
  .el-button {
    width:100px;
    margin-right:10px;
  }
}

</style>
