import Vue from 'vue';
import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../../UiResources/events';
import {uuid, saveDocument} from '../../../../../Frontend/Helpers';
import Strings from '../../../../UiResources/strings';
import {EDITABLE_TYPES, EXECUTION_STATE} from '../../../../../Common/VariousTypes';

export default class RunDialogMolecule extends AtomicDesign {


    addEventHandlers(){
        this.onGlobalEvent(EVENTS.TOOLBAR.RUN, this.openRunDialog);
        this.onGlobalEvent(EVENTS.TOOLBAR.RUN_DEFAULT, this.runDefault);
    }

    runDefault(){

        const runPresets = this.vue.openedDocument?.selectedResources?.runPresets;
        if(runPresets){
            const selectedEditable = this.vue.selectedItems?.editable;
            const runPreset = runPresets[selectedEditable.id];
            if (runPreset){
                this.vue.selectedPresetId = runPreset;
                this.run();
            }else {
                this.openRunDialog();
            }
        }       
    }


    openRunDialog(){
        const selectedEditable = this.vue.selectedItems?.editable;
        if (selectedEditable){
            const docLevelEditable = this.vue.openedDocument.editables.find(editable => editable.type == EDITABLE_TYPES.DOCUMENT_LEVEL);
            const docLevelProperties = docLevelEditable.properties["0"];
    
            switch(selectedEditable.type){
                case EDITABLE_TYPES.DOCUMENT_LEVEL:
                    this.msgbox(Strings.ERROR_MESSAGES.GENERIC_API_RUN_ERROR,this.MSGBOX_TYPE.OK, Strings.ERROR_MESSAGES.GENERIC_API_RUN_ERROR_TITLE);
                    return;
                case EDITABLE_TYPES.API: {
                    const properties = selectedEditable.properties;
                    const localHeaders = properties["0"].headers || [];
                    const docLevelHeaders = docLevelProperties.headers || [];
                    const allHeaders = localHeaders.concat(docLevelHeaders);
                    this.vue.allHeaders = allHeaders;
                    break;
                }
                case EDITABLE_TYPES.ADMIN:
                    break;
                case EDITABLE_TYPES.CRON:
                    break;
                default:
                    this.msgbox(Strings.ERROR_MESSAGES.UNKNOWN_RUN_ERROR,this.MSGBOX_TYPE.OK, Strings.ERROR_MESSAGES.UNKNOWN_RUN_ERROR_TITLE);
                    return;
            }
    
            this.createDefaultPresetIfNone();
            this.vue.selectedPresetId = this.vue.openedDocument?.selectedResources?.runPresets[selectedEditable.id];
            this.vue.visible = true;
        }

    }

    createDefaultPresetIfNone(){
        if (this.vue.presets.length == 0){
            this.createPresetWithDefaultValues("Default");
        }
    }

    async run(){
        
        const selectedEditableId = this.vue.selectedItems?.editable?.id;
        
        if (selectedEditableId){
            if (this.vue.selectedPresetId){
                
                const selectedEnvironmentId = this.vue.selectedPreset.environmentId;
                const selectedEnvironment = this.vue.openedDocument.environments.find(env => env.id == selectedEnvironmentId);
                const debugUrl = selectedEnvironment.config.debugUrl;

                if (!debugUrl){
                    this.msgbox("Debugger URL is not configured in the selected environment", this.MSGBOX_TYPE.OK,"Debugger URL not configured");
                    return;
                }

                this.vue.$set(this.vue.openedDocument.selectedResources.runPresets, selectedEditableId,this.vue.selectedPresetId);
                this.actions().setOpenedDocument({result:this.vue.openedDocument});
                const httpInputParams = this.vue.selectedPreset.environments["*"];   
        
                this.actions().setCompileErrors(null);
        
                await saveDocument(this);
                
                const editorProfileId = this.vue.editorProfile.id;
                const settings = this.getSettingToRun(this.vue, editorProfileId, httpInputParams,selectedEnvironmentId);
        
                this.vue.visible = false;
        
                const {error,result} = await this.backend(Strings.PROGRESS_INDICATOR.COMPILE).execute(settings);
                if (error){
                    this.vue.$notify({type: 'error', message: 'Error occured while communicating with compiler service', position: 'bottom-right', offset:0 });
                }else {
                    if (result.success){
                        this.startBusyIndicator("Executing");
                        const {json, inputs} = result;
                        const executorResult = await this.executeInDebugger(json, inputs,debugUrl);
                        this.stopBusyIndicator();
                        
                        if (executorResult.success){
                            this.actions().setExecutionFlow(executorResult);
                            this.emitGlobalEvent(EVENTS.RUN.SUCCESSFUL, {});
                        }else {
                            switch (executorResult.state){
                                case EXECUTION_STATE.ENGINE_EXECUTION_PREPARE:
                                    this.vue.$notify({type: 'error', message: 'Error occured while connecting to less-code engine', position: 'bottom-right', offset:0 });
                                    break;
                                case EXECUTION_STATE.RUNTIME:
                                    this.actions().setExecutionFlow(executorResult);
                                    this.emitGlobalEvent(EVENTS.RUN.SUCCESSFUL, {});
                                    break;
                            }
                            
                        }
                    }else {
                        switch (result.state){
                            case EXECUTION_STATE.COMPILE:
                                this.actions().setCompileErrors(result.result.trace);
                                this.vue.$notify({type: 'error', message: 'You have compilation errors', position: 'bottom-right', offset:0 });
                                break;
                        }
                    }
        
                }
            }else {
                this.msgbox("No preset selected to run", this.MSGBOX_TYPE.OK,"No preset selected");
            }

        }
       
    }

    async executeInDebugger(json, inputs, debugUrl){
        try {
            let {data} = await Vue.axios.post(debugUrl,{
                json,
                inputs
            });
            data.state = EXECUTION_STATE.RUNTIME;
            return data;
        }catch (e){
            if (e.code == "ECONNREFUSED"){
                return {
                    success: false,
                    state: EXECUTION_STATE.ENGINE_COMMUNICATE,
                    result : "Error communicating with less-code engine"
                }   
            }else {
                return {
                    success: false,
                    state: EXECUTION_STATE.ENGINE_EXECUTION_PREPARE,
                    result : "Unknown error occured in less-code engine"
                }   
            }        
        }
        
    }

    dialogClosed(){
        this.vue.allHeaders = [];
        this.vue.selectedPresetId = null;
    }

    presetSelected(val){
        if (val === "..."){
            this.newPreset();
        }
    }

    async newPreset(){
        
        try {
            this.vue.selectedPresetId = null;

            const {value} = await this.vue.$prompt('Please enter the name for the new preset', 'New preset', {
                confirmButtonText: 'OK',
                cancelButtonText: 'Cancel'
            });
            
            if (value){
                this.createPresetWithDefaultValues(value);
            }
        }catch (e){
            console.log();
        }
    }

    createPresetWithDefaultValues(name){
        const editorProfileId = this.vue.editorProfile.id;
        const selectedEditableId = this.vue.selectedItems.editable.id;
        let newPreset = Vue.observable({
            id: uuid(),
            name,
            editableId: selectedEditableId,
            type : "run",
            environmentId:"dev",
            environments : this.getDefaultSettingsForNewPreset(editorProfileId)
        });
        
        const openedDocument = this.vue.openedDocument;
        openedDocument.settings.push(newPreset);
        
        this.vue.$set(this.vue.openedDocument.selectedResources.runPresets, selectedEditableId,newPreset.id);
        this.actions().setOpenedDocument({result: openedDocument});

        Vue.nextTick(()=> {
            this.vue.selectedPresetId = newPreset.id;
        });
    }

    getDefaultSettingsForNewPreset(){
        return {
            "*" : {
                headers: {},
                pathParameters: {},
                queryParameters: {},
                postBody: "{}"
            }
        };
    }

    validate(){
        let messages = [];

        return messages;
    }

    getSettingToRun(vue,editorProfileId,selectedPreset,environmentId){
        return {
            openedDocumentId: vue.openedDocument.id,
            selectedEditableId: vue.selectedItems?.editable?.id,
            editorProfileId,
            selectedPreset,
            environmentId
        }
    }

    async deletePreset(){
        const result = await this.msgbox("Are you sure to delete this preset?", this.MSGBOX_TYPE.YES_NO, "Delete preset");

        if(result == this.MSGBOX_BUTTONS.YES) {
            const presetIndex = this.vue.openedDocument.settings.findIndex(setting => setting.id == this.vue.selectedPresetId);
            if (presetIndex !== -1){
                this.vue.openedDocument.settings.splice(presetIndex, 1);
            }
            this.vue.selectedPresetId = null;
        }
    }
}