<template>
  <div class="constructor-dialog-molecule">
    <el-dialog title="Object Constructor" :visible.sync="visible" top="40px" width="1100px" @close="dialogClosed" :close-on-click-modal="false">
      <div class="constructor-dialog-molecule__dialog">
        
        <div class="constructor-dialog-molecule__dialog__selector">
          <label>Object Presets</label>
          <el-select placeholder="" v-model="selectedPresetId" @change="presetSelected" size="mini">
            <el-option :value="null" label=""></el-option>
              <el-option v-for="preset in constructorPresets" :key="preset.id" :label="preset.name" :value="preset.id"></el-option>
              <el-option value="..." label="[Create New]"></el-option>
          </el-select>
          <img v-if="selectedPreset" v-on:click="deletePreset()" src="../../../../../Frontend/public/assets/toolbar/trash-alt-solid.svg" />  
        </div>

        <div class="constructor-dialog-molecule__dialog__message" v-if="!selectedConfig">
          <h3>To define the data you need to retrive, you need to select a preset or create a new one from the drop down on the top</h3>
        </div>

        <div class="constructor-dialog-molecule__dialog__body" v-if="selectedConfig">
          <div class="constructor-dialog-molecule__dialog__body__structure lc-custom-scroll">
            <JsonEditorAtom v-if ="selectedEntity" :schemaInfo="schemaInfo" 
            :selectedConfig="selectedConfig" 
            :entity="selectedEntity" 
            :isArray="true" 
            :dataTypes="dataTypes" 
            />
          </div>

          <div class="constructor-dialog-molecule__dialog__body__config lc-custom-scroll">
            
            <table class="constructor-dialog-molecule__dialog__body__config__table">
              <tr>
                <td class="key">
                  <label>Preset Name</label>
                </td>
                <td class="value">
                  <el-input size="mini" v-model="selectedPreset.name"></el-input>
                </td>
              </tr>

              <tr >
                <td class="key">
                  <label>Entity Name</label>
                </td>
                <td class="value">
                  <el-select placeholder="" v-model="selectedConfig.entityId" size="mini">
                    <el-option :value="null" label=""></el-option>
                    <el-option v-for="entity in schemaInfo.entities" :key="entity.id" :label="entity.name" :value="entity.id"></el-option>
                  </el-select>
                </td>
              </tr>
            </table>

          </div>
        </div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="visible = false">Cancel</el-button>
        <el-button type="primary" v-on:click="okClicked()">Ok</el-button>
      </span>

    </el-dialog>
  </div>
</template>

<script>
import JsonEditorAtom from './JsonEditorAtom/JsonEditorAtom';
import ObjectConstructorDialogMolecule from './ObjectConstructorDialogMolecule.hook'
import {DATA_TYPES,SCHEMA_TYPES} from '../../../../../Common/VariousTypes';
const dataTypes = {};
DATA_TYPES[SCHEMA_TYPES.MY_SQL].forEach(dt=>{
  dataTypes[dt.id] = {...dt}
});

const hook = new ObjectConstructorDialogMolecule();
import {getSchemaInfo} from '../../../../BackendServices/Converters/DocumentInformationRetriever';

export default {
  name: 'ObjectConstructorDialogMolecule',
  beforeCreate() { hook.hook(this); hook.addEventHandlers();},
  beforeDestroy() { hook.unsubscribeGlobalEvents(); },
  methods: hook.methods("okClicked","dialogClosed","presetSelected","itemSelected","deletePreset"),
  components:{
    JsonEditorAtom
  },
  computed: {
    selectedConfig(){
      const environmentConfig = this.selectedPreset?.environments["*"];
      return environmentConfig;
    },
    selectedPreset(){
      return this.constructorPresets?.find(preset => preset.id == this.selectedPresetId);
    },
    selectedEntity(){
      if (this.selectedConfig){
        const entityId = this.selectedConfig.entityId;
        if (entityId){
          if (!this.selectedConfig[entityId]){
            this.$set(this.selectedConfig,entityId,{});
          }
          return this.schemaInfo.entities.find(entity => entity.id == entityId);
        }
      }
      return null;
      
    },
    schemaInfo(){
      const schema = this.openedDocument?.editables.find(editable => editable.id == this.schemaId);
      return getSchemaInfo(schema);
    },
    constructorPresets(){
      return this.openedDocument?.settings.filter(setting => setting.type == "objectConstructor") ;
    },
    ...hook.getters("selectedItems","openedDocument")
  },
  mounted(){
    
  },
  data() {
      return { 
        selectedPresetId:null,
        selectedEntityId:null,
        schemaId:null,
        visible: false,
        sampleJson: [],
        dataTypes
      }
  }
}

</script>

<style scoped lang="scss">

.constructor-dialog-molecule {

  &__dialog {

    &__selector {
      display: flex;

      label {
        padding-top:10px;
        width:150px;
      }
      .el-select{
        width:100%;
      }

      img {
        height:16px;
        margin-left:10px;
        margin-top:5px;
        margin-right:4px;
        cursor: pointer;
      }

      margin-bottom:20px;
    }

    &__message {
      height:400px;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0.7;
    }

    &__body {
      display: flex;
      
      &__structure {
        overflow:auto;
        overflow-x:scroll;
        width: 650px;
        height:360px;
        background-color: rgb(20, 20, 20);
        padding:20px;
      }

      &__config {
        margin-left:25px;
        width:420px;
        height:400px;
        background-color: rgb(20, 20, 20);
        overflow-y:auto;

        &__table{
          padding:10px;

          tr{
            height:42px;
            vertical-align: top;
            th {
              text-align: left;
            }

            td.key {
              padding-right:30px;
              width:110px;
            }

            td.value {
              .el-input {
                width:190px;
              }

              .el-select {
                width:190px;
              }
            }
          }
        }

      }
    }
  }

  .dialog-footer {
    .el-button {
      width:100px;
    }
  }

}

</style>
